import React, { Component } from 'react';
import {Card} from 'material-ui/Card';
import './index.css';
import RaisedButton from 'material-ui/RaisedButton';
import Typist from 'react-typist';
import GA from '../../lib/utils/GA';
import me from '../../lib/constants/me';

const images = {
  avatar: require('../../assets/avatar/cl-min.jpg'),
  bg: require('../../assets/bg.jpg'),
};

const styles = {
  avatar: {
    width: 120,
    height: 120,
    borderRadius: 60,
    maxWidth: 120,
  },
  content: {
    paddingBottom: 12,
    paddingLeft: 12,
    height: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    color: 'white',
  },
};

class About extends Component {
  state = {
    typing: true,
  }

  done = () => {
    this.setState({ typing: false }, () => {
      this.setState({ typing: true })
    });
  }

  render() {
    return (
      <div className="bg-section" style={styles.content}>
        <div className="bg-main" />
        <div className="container">
          <div className="intro">
            <div>
              Build your next
              {this.state.typing &&
                <Typist
                  cursor={{
                    show: true,
                    blink: true,
                    element: '|',
                    hideWhenDone: true,
                    hideWhenDoneDelay: 500,
                  }}
                  onTypingDone={this.done}
                  // loop
                  // options={{
                  //   strings: ['mobile', 'MVP', 'iOS', 'android'],
                  //   autoStart: true,
                  //   loop: true,
                  // }}
                >
                  <span> mobile</span>
                  <Typist.Delay ms={2000} />
                  <Typist.Backspace count={6} />
                  <span>MVP</span>
                  <Typist.Delay ms={2000} />
                  <Typist.Backspace count={3} />
                  <span>iOS</span>
                  <Typist.Delay ms={2000} />
                  <Typist.Backspace count={3} />
                  <span> android</span>
                  <Typist.Delay ms={2000} />
                  <Typist.Backspace count={7} />
                </Typist>
              }
              app with React Native
            </div>
              <a
                className="contact-btn"
                href={me.mailto}
                onClick={() => {GA.trackLinkEvent('contactEmail')}}
              >
                Contact me
              </a>
          </div>
        </div>
      </div>
    );
  }
}

export default About;



