import React, { Component } from 'react';
import './index.css';
import {Card, CardHeader, CardText} from 'material-ui/Card';

const images = {
  shu: require('../../assets/avatar/shu.jpeg'),
  deanvern: require('../../assets/avatar/deanvern.jpeg'),
  edvin: require('../../assets/avatar/edvin.jpg'),
  rahim: require('../../assets/avatar/rahim.jpeg'),
  joerg: require('../../assets/avatar/joerg.jpeg'),
};

const styles = {
  item: {
    marginBottom: 30,
  },
};

const recommendations = [
  {
    id: 'shusaku',
    name: 'Shusaku Ueda',
    title: 'Head of Seekers',
    text: `Project: React Native Mobile App Development for Seekers (freelance recruiter's platform in Malaysia)
Nature of App: Freelance Recruiters' Support Application
Team: 3: Chin (this guy), Internal backend developer, internal app designer
App Link: https://play.google.com/store/apps/details?id=my.com.seekers.app
Development Period: 2 months + 1 month for debugging

Since I am not fully a developer, and our team was not familiar with React Native, Chin was helpful because:-
- Polite
- Good at explaining the things he can do and difficult to do (and come up with alternative ideas quickly)
- Fast Responce
- Dedicated
- Good time management (finished on time)
- Good support (after finishing the payment term and getting into debugging term, he was still with us)

The quality of application is satisfying at this point. Since we just launched, we never know the reputation from new users though, I think it was a good decision to develop with React Native because it's fast enough and easy to operate (upload new jobs or information) from single backend and reveal on both Android and iOS at the very same time with exactly same appearance.
    `,
    avatar: images.shu,
  },
  {
    id: 'deanvern',
    name: 'Goh Dean Vern',
    title: 'Ecommerce Business Owner',
    text: `A high responsibility person and developer i have working with for almost 1.5 years was corporate with chin loong on an E-commerce apps startup.
The best part we found out interesting regarding Chin loong is he always able to think from different dimension especially from business perspective, this definitely giving client best return on either reducing the cost or those unnecessary developing and waste it.

chin loong actively looking for solution and suggest different way in solving problem. this purely create a great value for our team which are not strong in IT knowledge, and it enable us a lot to focus on other business matter.

chin loong usually able to interpret clearly and logically the instruction we given, he will analyse all the instruction and give the best advise for us whether it is workable or it is just unuseful.  Chin loong always involve in development brainstorming with our team which this fully increase efficiency in our project from phase to phase.

Willingness and passion to help us continue modify and fix the bugs whenever our apps met any difficulties.`,
    avatar: images.deanvern,
  },
  {
    id: 'edvin',
    name: 'Edvin Fladen',
    title: 'Entrepreneur',
    text: `A great developer and a nice person. This is my impression from working with Chin Loong.

I had the pleasure of working with Chin Loong on a MVP project based on React Native. His skills, speed and professionalism where beyond my expectation. While juggling two other projects, he was still able to come up with solutions and he truly cared for the success of the project.

He is easy to collaborate with, and I am confident Chin Loong will do great things in the future.`,
    avatar: images.edvin,
  },
  {
    id: 'rahim',
    name: 'Rahim Rahman',
    title: 'Senior Software Developer at Aptos Retail',
    text: `I have worked with Chin Loong on a few React-Native projects and no matter how complex the issues assigned to him, he has always been able to come up with solutions that I have confidence in. He has been handling his tasks really well, willing to accept feedback and always eager to learn new things.

Hands down, he is one of the best software engineers I've had the privilege of working with in my career.`,
    avatar: images.rahim,
  },
  {
    id: 'joerg',
    name: 'Jörg Ossenkopp',
    title: 'Head of Frontend bei Parkling',
    text: `Chin Loong is a very nice and bright guy, great to work with. He is resourceful and knowledgeable, a quick learner. He helped us to solve some complex React Native problems in our app and built our website with Ruby on Rails.

I would always work again with him, with pleasure, and I can recommend him wholeheartedly.`,
    avatar: images.joerg,
  },
];

class Recommendation extends Component {
  renderRecommendation(recommendation, index) {
      return (
        <div key={`${recommendation.id}-${index}`} className="col-md-12" style={styles.item}>
          <Card key={recommendation.id} style={{ fontFamily: 'Raleway'}}>
            <CardHeader
              title={recommendation.name}
              subtitle={recommendation.title}
              // avatar={recommendation.avatar}
              avatar={(
                <img className="avatar-shadow" src={recommendation.avatar} alt="" />
              )}
              style={{display: 'flex'}}
              textStyle={{paddingRight: 0}}
            />
            <CardText>
              <div style={{whiteSpace: 'pre-wrap'}}>
                {recommendation.text}
              </div>
            </CardText>
          </Card>
        </div>
      );

    return (
      <div key={`${recommendation.id}-${index}`} className="col-md-4" style={styles.item}>
        <Card key={recommendation.id} style={{ fontFamily: 'Raleway'}}>
          <CardHeader
            title={recommendation.name}
            subtitle={recommendation.title}
            // avatar={recommendation.avatar}
            avatar={(
              <img className="avatar-shadow" src={recommendation.avatar} alt="" />
            )}
            style={{display: 'flex'}}
            textStyle={{paddingRight: 0}}
          />
          <CardText>
            <div style={{whiteSpace: 'pre'}}>
              {recommendation.text}
            </div>
          </CardText>
        </Card>
      </div>
    );
  }

  render() {
    return (
      <div className="section">
        <div className="container">
          <div className="main-title">
            <h4 className="">
              Recommendations
            </h4>
            <div className="main-title-underline" />
          </div>

          <div className="row">
            {recommendations.map((recommendation, index) => (
              this.renderRecommendation(recommendation, index)
            ))}
          </div>
        </div>
      </div>
    );
  }
}

export default Recommendation;



